<template>
  <div class="user-table">
    <el-table
      :data="userData"
      :stripe="true"
      :highlight-current-row="true"
      :height="userTableHeight"
      size="mini"
      border
      style="width: 100%">
      <el-table-column fixed type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label="">
              <span>{{ props.row.name }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.sex }}</span>
            </el-form-item><br>
            <el-form-item label="">
              <span>{{ props.row.mobile }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.birthdayF }}</span>
            </el-form-item><br>
            <el-form-item label="">
              <span>{{ props.row.department }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.job }}</span>
            </el-form-item><br>
            <el-form-item label="">
              <span>{{ props.row.idCardNumber }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.joinDateF }}</span>
            </el-form-item><br>
            <el-form-item label="">
              <span>{{ props.row.transferDateF }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.leaveDateF }}</span>
            </el-form-item><br>
            <el-form-item label="">
              <span>{{ props.row.detailAddress }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.remark }}</span>
            </el-form-item><br>
            <el-form-item label="">
              <span>{{ props.row.urgent }}</span>
            </el-form-item><br>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column
        prop="name"
        label=""
        width="80">
      </el-table-column>
      <el-table-column
        prop="mobile"
        label=""
        width="100">
      </el-table-column>
      <el-table-column
        prop="sex"
        label=""
        width="80">
      </el-table-column>
      <el-table-column
        prop="birthdayF"
        label=""
        width="100">
      </el-table-column>
      <el-table-column
        prop="department"
        label=""
        width="100">
      </el-table-column>
      <el-table-column
        prop="job"
        label=""
        width="100">
      </el-table-column>
      <el-table-column
        prop="idCardNumber"
        label=""
        width="150">
      </el-table-column>
      <el-table-column
        prop="joinDateF"
        label=""
        width="100">
      </el-table-column>
      <el-table-column
        prop="transferDateF"
        label=""
        width="100">
      </el-table-column>
      <el-table-column
        prop="leaveDateF"
        label=""
        width="100">
      </el-table-column>
      <el-table-column
        prop="detailAddress"
        label=""
        width="200">
      </el-table-column>
      <el-table-column
        fixed="right"
        label=""
        width="100">
        <template slot-scope="scope" v-if="contactPriv.user">
          <el-button type="text" size="mini" @click="handleClick(scope.row)"></el-button>
          <!--<el-button type="text" size="mini" @click="delUser(scope.row)"></el-button>-->
        </template>
      </el-table-column>
    </el-table>

    <el-dialog
      width="50%"
      title=""
      :visible.sync="dialogVisible"
      :before-close="handleClose">
      <user-form
        ref="editUserPanel"
        @toggle="dialogVisible = false">
      </user-form>
    </el-dialog>
  </div>
</template>

<script>
  import { mapState, mapGetters, mapMutations } from 'vuex'
  import UserForm from './UserForm'

  export default {
    name: 'user-table',

    methods: {
      ...mapMutations(['setState']),

      handleClose(done) {
        this.$confirm('').then(_ => {
          done()
        }).catch(_ => {})
      },

      handleClick(row) {
        this.dialogVisible = true
        this.setState({
          key: 'handleType',
          value: 'edit'
        })
        this.setState({
          key: 'currentUser',
          value: row
        })
        this.$nextTick(()=> {
          this.$refs['editUserPanel'].form = {
            ...row,
            birthday: row.birthday ? new Date(row.birthday) : '',
            joinDate: row.joinDate ? new Date(row.joinDate) : '',
            transferDate: row.transferDate ? new Date(row.transferDate) : '',
            leaveDate: row.leaveDate ? new Date(row.leaveDate) : '',
            department: {
              label: row.department,
              id: row.departmentId
            }
          }
        })
      },

      delUser(item) {
        this.$confirm('').then(()=> {
          console.log(item)
        })
      }
    },

    mounted() {
      if (this.userTableHeight === 0) {
        this.setState({
          key: 'userTableHeight',
          // value: document.body.clientHeight - 200
          value: window.screen.availHeight - 200
        })
      }
    },

    computed: {
      ...mapState(['userTableHeight']),
      ...mapGetters(['contactPriv', 'userList']),
    },

    props: ['userData'],

    data() {
      return {
        dialogVisible: false
      }
    },

    components: {
      UserForm
    }
  }
</script>

<style>
  .demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 300px;
  }
  .el-form--inline .el-form-item__label {
    color: #aaa;
  }
</style>



